@import url('https://fonts.cdnfonts.com/css/valorant');
@import url('https://fonts.googleapis.com/css2?family=Afacad+Flux:wght@100..1000&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&family=Oswald:wght@700&family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.allagents{
  width: 100%; 
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: 	#ece8e3;
  padding: 0 20px;

}
.container-yours-agents {
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

.section-agents-1 {
  width: 55%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 24px;
  height: 400px;
}

.img-agents {
  width: 650px;
  margin-top: 10px;
}

.section-agents-2 {
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}

.title-agents {
  font-size: 76px;
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: -2px;
  color: #ff283a;
  text-align: center;
  text-shadow: 2px 8px 12px rgba(81, 0, 0, 0.4);
}

.subtitle-agents {
  font-size: 34px;
  font-family: 'Oswald', sans-serif;
  color: #555;
  margin-bottom: 10px;
  text-align: center;
}

.description-agents {
  font-size: 20px;
  font-family: "Afacad Flux", sans-serif;
  color: #555;
  margin: 0 10px 0 0;
  text-align: center;
}

.btn-agents {
  padding: 20px 40px;
  background-color: #ff283a;
  color: white;
  font-size: 20px;
  font-family: 'VALORANT', sans-serif;
  text-align: center;
  cursor: pointer;
  transition: all 0.4s ease;
  width: fit-content;
  text-decoration: none;
  display: block;
  margin: 30px auto;
  position: relative;
  overflow: hidden;
  z-index: 1;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);

}

.btn-agents:hover {
  transform: scale(1.1);
  box-shadow: 0 0 15px #333;
  background-color: #333;
}

.btn-agents::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300%;
  height: 300%;
  transition: width 0.4s ease, height 0.4s ease, top 0.4s ease, left 0.4s ease, background-color 0.4 ease;
  border-radius: 50%;
  z-index: -1;
  transform: translate(-50%, -50%);
}

.btn-agents:hover::before {
  width: 120%;
  height: 120%;
  top: 50%;
  left: 50%;
  background-color: #333;
}

@media (min-width: 1024px) {
  .container-yours-agents {
    width: 90%;
    gap: 40px;
  }
  
  .section-agents-1 {
    width: 55%;
  }
  
  .section-agents-2 {
    width: 45%;
  }
  
  .title-agents {
    font-size: 76px;
  }

  .subtitle-agents {
    font-size: 34px;
  }

  .description-agents {
    font-size: 20px;
  }

  .btn-agents {
    font-size: 20px;
    padding: 20px 40px;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .allagents {
    height: auto;
    padding: 20px;
  }
  
  .container-yours-agents {
    flex-direction: column;
    gap: 20px;
  }
  
  .section-agents-1, .section-agents-2 {
    width: 100%;
  }

  .img-agents {
    width: 65%;
  }

  .title-agents {
    font-size: 56px;
  }

  .subtitle-agents {
    font-size: 28px;
  }

  .description-agents {
    font-size: 18px;
  }

  .btn-agents {
    font-size: 18px;
    padding: 15px 30px;
  }
}

@media  (max-width: 767px) {
  .allagents {
    height: auto;
    padding: 15px;
  }
  
  .container-yours-agents {
    flex-direction: column;
    gap: 15px;
  }

  .section-agents-1, .section-agents-2 {
    width: 100%;
  }

  .img-agents {
    width: 55%;
  }

  .title-agents {
    font-size: 40px;
  }

  .subtitle-agents {
    font-size: 24px;
  }

  .description-agents {
    font-size: 16px;
    margin: 0 10px;
  }

  .btn-agents {
    font-size: 16px;
    padding: 10px 20px;
  }
}

@media (max-width: 480px) {
  .img-agents {
    width: 95%;
  }
}