@import url('https://fonts.cdnfonts.com/css/valorant');
@import url('https://fonts.googleapis.com/css2?family=Afacad+Flux:wght@100..1000&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&family=Oswald:wght@700&family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.weValorant{
  width: 100%; 
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: 	#ece8e3;
  padding: 0 20px;
}

.container-we {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

.section-valo-1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}

.title-valo{
  font-size: 56px;
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
  text-align: center;
  font-weight: 900;
  letter-spacing: -2px;
  color: #ff283a;
  text-shadow: 2px 8px 12px rgba(81, 0, 0, 0.4);
}

.subtitle-valo {
  font-size: 34px;
  font-family: 'Oswald', sans-serif;
  color: #555;
  margin-bottom: 10px;
  text-align: center;
  }

.description-valo{
  width: 650px;
  font-size: 20px;
  font-family: "Afacad Flux", sans-serif;
  color: #555;
  text-align: center;
}

.btn-valo {
  padding: 20px 40px;
  background-color: #ff283a;
  color: white;
  font-size: 20px;
  font-family: 'VALORANT', sans-serif;
  text-align: center;
  cursor: pointer;
  transition: all 0.4s ease;
  width: fit-content;
  text-decoration: none;
  display: block; 
  margin: 30px auto; 
  position: relative;
  overflow: hidden;
  z-index: 1;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
}

.btn-valo:hover {
  transform: scale(1.1); 
  box-shadow: 0 0 15px #333;
  background-color: #333;
}
.btn-valo::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300%;
  height: 300%;
  transition: width 0.4s ease, height 0.4s ease, top 0.4s ease, left 0.4s ease, background-color 0.4 ease;
  border-radius: 50%;
  z-index: -1; 
  transform: translate(-50%, -50%);
}

.btn-valo:hover::before {
  width: 120%;
  height: 120%;
  top: 50%;
  left: 50%;
  background-color: #333;
}

.section-valo-2 {
  width: 80%;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.3);
}

@media (max-width: 1024px) {
  .weValorant {
    flex-direction: column;
    height: auto;
    padding: 20px;
  }

  .container-we {
    flex-direction: column;
    gap: 20px;
  }

  .title-valo {
    font-size: 42px;
  }

  .subtitle-valo {
    font-size: 28px;
  }

  .description-valo {
    width: 100%;
    font-size: 18px;
  }

  .btn-valo {
    padding: 15px 30px;
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .title-valo {
    font-size: 32px;
  }

  .subtitle-valo {
    font-size: 24px;
  }

  .description-valo {
    width: 100%;
    font-size: 16px;
  }

  .btn-valo {
    padding: 10px 20px;
    font-size: 16px;
  }

  .section-valo-2 {
    width: 100%;
    font-size: 18px;
  }
}
